import { IUpdatable, ISceneObject } from "../scene/IScene.js";
import { Move, MovementDirection } from "../utils/MovementUtils.js";

export class LinearMovementAI implements IUpdatable {
    protected readonly _obj: ISceneObject;
    protected readonly _direction: MovementDirection;
    protected readonly _speed: number;

    public constructor(controlledObject: ISceneObject, speed: number, direction: MovementDirection) {
        this._obj = controlledObject;
        this._speed = speed;
        this._direction = direction;
    }

    public update(diff: number): void {
        this._obj.position = Move.toDirection(this._obj.position, this._direction, this._speed * diff / 1000);
    }
}